@font-face {
  font-family: "Pretendard-Regular";
  src: url("https://cdn.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff")
    format("woff");
  font-weight: 900;
  font-style: normal;
}
* {
}
#canvas {
  font-family: "Pretendard-Regular";

  width: 85vw;
  margin-left: auto;
  margin-right: auto;
}

h1 {
  font-family: "Pretendard-Regular";
}
.bodyCanvas {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
/* 네비게이션 */
.navbar {
  width: 15%;
  height: 500px;
  padding-left: 20px;
  border: none;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  /* align-items: center; */

  background-color: white;
  border-right: 0.8px solid gainsboro;
}
.navbarMenu {
  background-color: white;

  text-decoration: none;
  margin-top: 10px;
  margin-bottom: 40px;
}

/* 홈 */
.homeCanvas {
  width: 80%;
  margin: 0;
  padding-left: 30px;
  display: flex;
  flex-direction: column;
}
.profile {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 30px;
  margin-bottom: 20px;
}
.profileDetail {
  margin-right: 30px;
}

/* about */
.aboutCanvas {
  width: 80%;
  margin: 0;
  padding-left: 30px;
}
.summaryCanvas {
  width: 80%;
  margin: 0;
  padding-left: 30px;
}
.detailCanvas {
  width: 80%;
  margin: 0;
  padding-left: 30px;
}

